import * as React from 'react';
import { ContentPage } from 'piral-docs-tools/components';

      import { PageContent, Markdown, getPageLayout } from 'piral-docs-tools/components';

      const PageLayout = getPageLayout("blog");
      const link = "https://github.com/AngleSharp/anglesharp.github.io/tree/main/docs/blog/2015-12-31-anglesharp-v094-released.md";
      const html = `
<p>Finally, after weeks of delays and many discussions, AngleSharp v0.9.4 is available. There have been some bug fixes and improvements. Most importantly, these are encoding and insertion pointer fixes.</p>
<p>However, AngleSharp v0.9.4 is in fact more than just a minor release. It could be considered a bridge release, as many internal things have been changed for the better. The upcoming version(s) will continue to walk the path enabled by v0.9.4.</p>
<p>Features, such as the behavior of <code>OpenAsync</code> (delayed until embedded resources finished loading) or the virtual file system are already partially available. The CSSOM will also see more updates and will be enhanced with further helpers to modify the OM with objects instead of raw strings.</p>
<p>On the API side a lot new things will come up. Many internal concepts will be made public and the parsing can be expected to become even more flexible.</p>
<p>Last but not least, the performance will be improved. While HTML is already quite decent (but could be improved in some scenarios), CSS has plenty of room for improvement.</p>
`;
